* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

a {
    text-decoration: none;
    color: #FFFF;
}

ul,
p,
h1,
h2,
h3 {
    margin: 0;
}

ul {
    list-style: none;
}

button {
    cursor: pointer;
}

.container {
    max-width: 80%;
    margin: 0 auto;
}